body {
  margin: 0;
  background-color: rgba(0, 0, 0, 0.678);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

a:hover {
  opacity: .7;
}

a:active {
  opacity: 0.4;
}

li {
  list-style: none;
}

input {
  font-size: 20px;
  padding: 10px 20px;
  min-width: 500px;
  margin: 20px auto;
  display: block;
  text-align: center;
  background-color: rgba(34, 28, 28, 0.862);
  border-radius: 30px;
  color: white;
}
input.error {
  border: 2px solid red;
  color: red;
}
h2{
  position: absolute;
  left: 38%;
  font-size: 40px;
}

.movies {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)) ;
  grid-gap: 15px;  
  padding: 0 100px 50px;
}
.movies li {
  position: relative;
}
.movies li img {
  width: 100%;
}
.movies li p {
  font-size: 15px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.9);
  position: absolute;
  top: 0;
  width: 100%;
  display: none;
}
.movies li:hover p {
  display: block;
  border-radius: 20px 20px 0px 0px;
  background-color: rgba(121, 179, 193, 0.854);
}
#img{
  border-radius: 20px;
  height: 300px;
}
.details{
  font-weight: 700;
  text-align: center;
}